@import url("https://fonts.googleapis.com/css?family=Alegreya+Sans+SC:300|Roboto+Slab|Montserrat:500");
img {
  max-width: 100%;
  margin: 0 auto;
  max-height: 14rem; }

body.mobile nav:not(.small) {
  width: 100%; }
  body.mobile nav:not(.small) .menu-item {
    width: calc(100% - 1rem); }
  body.mobile nav:not(.small) .menu-item .sub-menu {
    width: 100%;
    margin-left: -20.6rem; }

body.mobile .nav.small .menu-item .sub-menu {
  margin-left: -15.6rem !important; }

body.dark {
  background: #141414;
  color: #e6e6e6; }
  body.dark .section-links a {
    color: #e6e6e6 !important; }
  body.dark img {
    opacity: .8; }
    body.dark img:hover {
      opacity: 1; }
  body.dark .border {
    border-color: #bababa; }
  body.dark nav {
    background-color: #141414;
    color: #e6e6e6; }
  body.dark .box-shadow {
    -webkit-box-shadow: 0 0 10px 0 black;
    -moz-box-shadow: 0 0 10px 0 black;
    box-shadow: 0 0 10px 0 black; }

.flex {
  display: flex;
  flex-wrap: wrap; }
  @media screen and (max-width: 75rem) {
    .flex {
      justify-content: center; } }

.section,
.section-content,
.footer,
p,
h1,
h2 {
  clear: both; }

h1, h2, h3, h4, h5, h6 {
  font-family: 'Alegreya Sans SC', sans-serif;
  text-transform: uppercase;
  text-align: left;
  width: 100%; }

p {
  font-family: 'Roboto Slab',serif;
  width: 100%; }

a, a:visited {
  color: #4baddd;
  text-decoration: none; }
  a:hover, a:visited:hover {
    text-decoration: underline; }

body {
  background: #f5f5f5;
  color: #141414;
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;
  margin: 0;
  padding: 0;
  font-size: 14px; }

br {
  margin-bottom: 1rem; }

code {
  font-family: source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace; }

.menu-item:hover > .sub-menu-indicator svg[data-icon='angle-down'],
.menu-item.active > .sub-menu-indicator svg[data-icon='angle-down'],
.menu-item.sub-active > .sub-menu-indicator svg[data-icon='angle-down'] {
  transform: rotate(180deg); }

.menu-item:hover > .sub-menu,
.active > .sub-menu,
.sub-active > .sub-menu {
  max-height: 20rem !important; }

.sub-active, .sub-menu {
  background: #e6e6e6;
  color: #1e1e1e !important; }

.sub-active > .menu-item-wrapper {
  background: #d1d1d1; }

.active > .menu-item-wrapper {
  background: #bababa;
  color: #141414 !important; }

.icon {
  cursor: pointer;
  font-size: 1.3rem;
  width: 10rem;
  height: 1.5rem;
  margin-top: -.3rem; }
  .icon:hover div {
    transform: scale(1, 1); }
  .icon:hover .one {
    margin: .5rem 0 0 .85rem; }
  .icon:hover .two {
    margin: 0 0 0 3.5rem; }
  .icon:hover .three {
    font-size: .8rem;
    margin: .1rem 0 0 4rem; }
  .icon div {
    font-style: normal;
    position: absolute;
    transition: transform 0.26s ease-in-out, margin 0.26s ease-in-out; }
  .icon .one {
    font-size: .8rem;
    margin: 0 0 0 2rem; }
  .icon .two {
    color: #4baddd;
    margin: .2rem 0 0 6.5rem; }
  .icon .three {
    color: #4baddd;
    font-size: .7rem;
    margin: .6rem 0 0 2rem; }
  .icon .h {
    transform: scale(-1, 1); }
  .icon .vh {
    transform: scale(-1, -1); }

.box-shadow {
  -webkit-box-shadow: 0 0 10px 0 #c8c8c8;
  -moz-box-shadow: 0 0 10px 0 #c8c8c8;
  box-shadow: 0 0 10px 0 #c8c8c8; }

.tr {
  transition: 0.26s ease-in-out; }

nav {
  font-family: 'Montserrat',sans-serif;
  height: 100vh;
  left: 0;
  top: 0;
  overflow: hidden;
  position: fixed;
  transition: width 0.26s ease-in-out;
  width: 15rem;
  background-color: #f5f5f5; }
  nav.small {
    width: 2.8rem; }
    nav.small > .menu-item .sub-menu-indicator {
      opacity: 0; }
    nav.small ~ .content {
      padding-left: 2.8rem;
      width: calc(100% - 14.8rem); }
    nav.small > .menu-item .menu-item-wrapper span,
    nav.small > .settings span {
      opacity: 0; }
  nav.hidden {
    width: 0; }
  nav .menu-item {
    cursor: pointer;
    font-family: 'Montserrat',sans-serif;
    overflow: hidden;
    width: 15rem; }
    nav .menu-item[data-filler='true'] {
      opacity: 0;
      height: .6rem; }
    nav .menu-item .menu-item-wrapper {
      padding: 0.4rem;
      margin: 0;
      position: relative;
      max-height: 2rem;
      width: 100%; }
      nav .menu-item .menu-item-wrapper span {
        position: absolute; }
      nav .menu-item .menu-item-wrapper:hover > span {
        text-decoration: underline; }
      nav .menu-item .menu-item-wrapper i[data-state='off'] > svg {
        transform: rotate(90deg); }
      nav .menu-item .menu-item-wrapper i {
        padding: 0.4rem;
        overflow: hidden; }
    nav .menu-item .sub-menu-indicator {
      position: absolute;
      right: 1.5em;
      padding: .5rem;
      transition: 0.26s ease-in-out; }
      nav .menu-item .sub-menu-indicator svg {
        position: absolute;
        right: 0;
        transition: 0.26s ease-in-out; }
    nav .menu-item .sub-menu {
      max-height: 0;
      overflow: hidden;
      transition: margin 0.26s ease-in-out; }
      nav .menu-item .sub-menu .menu-item {
        font-size: .9em; }

.border {
  border-color: #707070; }

.content {
  font-family: 'Crete Round',serif;
  line-height: 1.5em;
  max-width: 50rem;
  overflow: hidden;
  padding-left: 14rem;
  width: calc(100% - 19rem);
  margin: 0 auto; }
  @media screen and (max-width: 50rem) {
    .content {
      width: calc(100% - 4.8rem) !important; } }
  .content div .header {
    width: 100%;
    padding: .4rem; }
    .content div .header .sub_header {
      margin: -1.4em 0 0 .7em;
      padding: 0;
      color: #4b4b4b; }
  .content div .section .section-content {
    justify-content: center;
    padding: 1rem; }
    .content div .section .section-content .section-image-wrapper {
      background-repeat: repeat;
      min-height: 25rem;
      width: 100%;
      justify-content: center; }
  .content div .section.section-large {
    background-repeat: repeat;
    min-height: 25rem;
    width: calc(100% - 2.6em);
    border: 0; }

div[data-background='star'] {
  animation: animatedBackground_bg 130s linear infinite;
  background-color: #141414;
  background-image: url(./resources/stars_bg.gif);
  color: #e6e6e6 !important; }
  div[data-background='star'] .section-image-wrapper {
    animation: animatedBackground 90s linear infinite;
    background-image: url(./resources/stars.gif); }
  div[data-background='star'] .section-links a {
    color: #e6e6e6 !important; }
  div[data-background='star'] p, div[data-background='star'] h1, div[data-background='star'] img {
    color: #e6e6e6; }

div[data-background='bionic'] .header {
  background-color: rgba(100, 255, 100, 0.3); }

.footer {
  padding: 2rem;
  width: calc(100% - 4rem);
  justify-content: center; }
  .footer p {
    font-size: .8rem;
    text-align: center; }

@keyframes animatedBackground {
  from {
    background-position: 0 0; }
  to {
    background-position: 640px 400px; } }

@-webkit-keyframes animatedBackground {
  from {
    background-position: 0 0; }
  to {
    background-position: 640px 400px; } }

@keyframes animatedBackground_bg {
  from {
    background-position: 0 0; }
  to {
    background-position: -640px -800px; } }

@-webkit-keyframes animatedBackground_bg {
  from {
    background-position: 0 0; }
  to {
    background-position: -640px 400px; } }
